"use client";

import Link from "next/link";
import { WalletButton } from "./WalletButton";
import { useAuth } from "@/lib/auth-context";

export function Header() {
  const { creator, isAuthenticated, walletConnected, needsSignup, isLoading } = useAuth();

  return (
    <header className="border-b border-zinc-800 bg-zinc-950/80 backdrop-blur-sm sticky top-0 z-50">
      <div className="max-w-5xl mx-auto px-4 h-16 flex items-center justify-between">
        <Link href="/" className="flex items-center gap-2">
          <div className="w-8 h-8 bg-gradient-to-br from-blue-500 to-purple-600 rounded-lg flex items-center justify-center">
            <svg
              className="w-5 h-5 text-white"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z"
              />
            </svg>
          </div>
          <span className="font-semibold text-lg text-zinc-100">Reclaim</span>
        </Link>

        <nav className="flex items-center gap-6">
          <Link
            href="/sign"
            className="text-sm text-zinc-400 hover:text-zinc-100 transition-colors"
          >
            Sign
          </Link>
          <Link
            href="/verify"
            className="text-sm text-zinc-400 hover:text-zinc-100 transition-colors"
          >
            Verify
          </Link>

          {/* Show creator info when authenticated */}
          {isAuthenticated && creator && (
            <Link
              href={`/creator/${creator.id}`}
              className="flex items-center gap-2 text-sm text-zinc-300 hover:text-zinc-100 transition-colors"
            >
              <div className="w-6 h-6 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-xs text-white font-medium">
                {creator.display_name?.[0]?.toUpperCase() || "?"}
              </div>
              <span className="hidden sm:inline">{creator.display_name}</span>
            </Link>
          )}

          {/* Show signup prompt when wallet connected but no account */}
          {walletConnected && needsSignup && !isLoading && (
            <Link
              href="/signup"
              className="text-sm px-3 py-1.5 bg-amber-500/10 text-amber-400 border border-amber-500/20 rounded-lg hover:bg-amber-500/20 transition-colors"
            >
              Complete Signup
            </Link>
          )}

          <WalletButton />
        </nav>
      </div>
    </header>
  );
}
